export default {
  // 局部刷新
  inject: ['reload'],
  data() {
    return {
      // 将修改的商品
      cateKeys: [],
      // 获取到的商品
      cateList: [],
      // 配置级联选择框
      props: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      // 标签页名称
      activeName: 'many',
      // 获取到的动态参数数据
      dtParamsList: [],
      // 获取到的静态属性数据
      jtParamsList: [],
      // 添加参数 对话框
      addDialogVisible: false,
      // 添加 验证规则
      rules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      },
      // 添加的参数数据
      addParamsForm: {},
      // 编辑 验证规则
      editRules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      },
      // 编辑 对话框 可视
      editDialogVisible: false,
      // 编辑 数据
      editParamsForm: {}
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取商品数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取数据失败')
      }
      this.cateList = res.data
    },
    // 监听分类选择框变化
    handleChange() {
      this.getParamsList()
    },
    // 监听标签页变化
    handleClick() {
      this.getParamsList()
    },
    // 获取参数数据
    async getParamsList() {
      // 未选中三级
      if (this.cateKeys.length !== 3) {
        this.cateKeys = []
        this.dtParamsList = []
        this.jtParamsList = []
        return
      }
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/`,
        { params: { sel: this.activeName } }
      )
      // 将attr_vals转换成数组
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        item.inputVisible = false
        item.inputValue = ''
      })
      if (this.activeName === 'many') {
        this.dtParamsList = res.data
      } else {
        this.jtParamsList = res.data
      }
    },
    // 添加参数
    addParams() {
      this.addDialogVisible = true
    },
    // 确认提交参数
    confirmAddParams() {
      this.$refs.addParamsRef.validate(async (valid) => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addParamsForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 201) {
          return this.$message.error('添加失败')
        }
        this.getParamsList()
        this.addDialogVisible = false
        this.$message.success('添加成功')
      })
    },
    // 重置 添加 对话框
    resetAddDialog() {
      this.$refs.addParamsRef.resetFields()
    },
    // 编辑参数
    editParams(row) {
      this.editParamsForm = row
      this.editDialogVisible = true
    },
    // 提交编辑
    confirmEditParams() {
      this.saveAttrVals(this.editParamsForm)
      this.editDialogVisible = false
    },
    // 重置 编辑 对话框
    resetEditDialog() {
      this.$refs.editParamsRef.resetFields()
    },
    // 删除参数
    async deleteParams(attrId) {
      console.log(this.cateId)
      console.log(attrId)
      const confirmResult = await this.$confirm(
        '该操作无法撤销,确定吗?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消')
      }
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${attrId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.$message.success('删除成功')
      this.getParamsList()
    },
    // tag参数请求
    async saveAttrVals(row) {
      row.attr_vals.push(row.inputValue)
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: this.activeName,
          attr_vals: row.attr_vals.join(',')
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('操作失败')
      }
      this.$message.success('操作成功')
      this.getParamsList()
      row.inputValue = ''
      row.inputVisible = false
    },
    // tag删除事件
    handleClose(row, i) {
      row.attr_vals.splice(i, 1)
      this.saveAttrVals(row)
    },
    // tag切换
    showInput(row) {
      row.inputVisible = true
      // 焦点自动落在input
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 提交tag修改
    handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      this.saveAttrVals(row)
    }
  },
  computed: {
    // 选择完商品再开放添加参数
    isBan() {
      if (this.cateKeys.length !== 3) {
        return true
      }
      return false
    },
    // 当前商品ID
    cateId() {
      if (this.cateKeys.length === 3) {
        return this.cateKeys[this.cateKeys.length - 1]
      }
      return null
    },
    // 对话框提示信息处理
    dialogText() {
      if (this.activeName === 'many') {
        return '动态参数'
      }
      return '静态属性'
    }
  }
}
