export default {
  data() {
    return {
      // 查询条件
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 商品分类数据
      cateList: [],
      // 总条数
      total: 0,
      // 为treetable列指定定义
      columns: [
        {
          label: '名称',
          prop: 'cat_name'
        },
        {
          label: '是否有效',
          type: 'template',
          template: 'isok'
        },
        {
          label: '排序',
          type: 'template',
          template: 'level'
        },
        {
          label: '操作',
          type: 'template',
          template: 'operate'
        }
      ],
      // 对话框可视
      cateDialogVisible: false,
      editDialogVisible: false,
      // 验证规则
      rules: {
        cat_name: [{ required: true, message: '请输入名称', trigger: 'blur' }]
      },
      // 对话框中 分类 数据
      catingForm: {
        cat_pid: 0,
        cat_name: '',
        cat_level: 0
      },
      // 对话框 父级 数据
      catingList: [],
      // 配置级联选择器
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover',
        checkStrictly: 'true'
      },
      // 选中的id
      cateKeys: [],
      // 编辑功能 表单数据
      editForm: {},
      // 编辑功能 验证规则
      editRules: {
        cat_name: [{ required: true, message: '名称不能为空', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取分类列表
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取数据失败')
      }
      this.cateList = res.data.result
      this.total = res.data.total
    },
    // 弹出对话框
    changeDialog() {
      this.getCatingList()
      this.cateDialogVisible = true
    },
    // 获取父类
    async getCatingList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取数据失败')
      }
      this.catingList = res.data
    },
    // 当选项发生变化时
    handleChange() {
      // length大于0 说明为一级以下等级
      if (this.cateKeys.length > 0) {
        this.catingForm.cat_pid = this.cateKeys[this.cateKeys.length - 1]
        this.catingForm.cat_level = this.cateKeys.length
      } else {
        this.catingForm.cat_pid = 0
        this.catingForm.cat_level = 0
      }
    },
    // 确认添加新分类
    async addNewCate() {
      const { data: res } = await this.$http.post('categories/', {
        cat_pid: this.catingForm.cat_pid,
        cat_name: this.catingForm.cat_name,
        cat_level: this.catingForm.cat_level
      })
      if (res.meta.status !== 201) {
        return this.$message.error('添加失败')
      }
      this.getCateList()
      this.cateDialogVisible = false
      this.$message.success('添加成功')
      console.log(res)
    },
    // 重置 添加 对话框
    resetAddDialog() {
      this.$refs.catingFormRef.resetFields()
      this.cateKeys = []
    },
    // 更改 修改功能 对话框
    editCate(info) {
      this.editForm = info
      this.editDialogVisible = true
    },
    // 确认编辑
    async confirmEdit() {
      const { data: res } = await this.$http.put(
        'categories/' + this.editForm.cat_id,
        { cat_name: this.editForm.cat_name }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('编辑失败')
      }
      this.getCateList()
      this.editDialogVisible = false
      this.$message.success('编辑成功')
    },
    // 删除
    async deleteCate(id) {
      const confirmResult = await this.$confirm(
        '该操作无法撤销,确认吗?',
        '提示',
        {
          confirmButtonText: '确认',
          cancelButtonText: '取消'
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消')
      }
      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.getCateList()
      this.$message.success('删除成功')
    },
    // 分页功能
    // 监听 pagesize 变化
    handleSizeChange(newsize) {
      this.queryInfo.pagesize = newsize
      this.getCateList()
    },
    // 监听 当前页码 变化
    handleCurrentChange(newnum) {
      this.queryInfo.pagenum = newnum
      this.getCateList()
    }
  }
}
